@use "global" as *;


.fv {
  height: 100vh;
  max-height: rem(896);
  background: url(../images/common/fv_bg.webp) no-repeat center center / cover;
  position: relative;

  @include mq(md) {
    max-height: rem(802);
    background: url(../images/common/fv_bg-sp.webp) no-repeat center center / cover;
  }
}

.fv__inner {
  height: 100%;
}

.fv__wrap {
  height: 100%;
  position: relative;
}

.fv__texts {
  color: $color-white;
  leading-trim: both;
  text-edge: cap;
  font-family: $font-family-ja-1;
  font-size: rem(31.791);
  font-weight: $bold;
  line-height: 1.1;

  display: flex;
  flex-direction: row-reverse;
  justify-content: center;
  align-items: flex-start;
  gap: rem(20);

  position: absolute;
  top: 50%;
  left: calc(50% - min(48vw, 27.04rem));  // *位置を制御
  transform: translateY(calc(-50% + rem(40)));  // *位置を制御

  @include mq(md) {
    font-size: rem(17.687);
    gap: rem(15);
  }
}

.fv__text01 {
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3); /* テキストに影を付ける */

  writing-mode: vertical-rl; /* 縦書きに変更 */
  text-orientation: upright; /* 日本語や漢字をそのままの形で表示 */
  width: rem(42); /* 横幅を適切に設定 */

  @include mq(md) {
    width: rem(16);
  }
}

.fv__text02 {
  margin-top: rem(80);
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3); /* テキストに影を付ける */

  writing-mode: vertical-rl; /* 縦書きに変更 */
  text-orientation: upright; /* 日本語や漢字をそのままの形で表示 */
  width: rem(30); /* 横幅を適切に設定 */
  padding: rem(10); /* テキストの周囲に余白を設定 */
}

.fv__logo {
  width: fit-content;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, calc(-50% + rem(41)));
}

.fv__logo img {
  aspect-ratio: 255 / 258;
  width: rem(255);
  height: rem(258);
}

.fv__reserve-circle {
  max-width: rem(213.67);
  width: 100%;

  position: absolute;
  bottom: rem(56.67);
  right: calc(50% - min(48vw, 40rem));  // *位置を制御
}

.fv__scroll-bar {
  position: absolute;
  bottom: 0;
  left: calc(50% - min(48vw, 36.5rem));  // *位置を制御
}
