@use "global" as *;


.title01 {
  display: block;
}

.title01__text {
@include mq(md) {
  color: $color-white;
  leading-trim: both;
  text-edge: cap;
  font-family: $font-family-ja-1;
  font-size: rem(12);
  font-weight: $bold;
  line-height: 1.4;
}
}

.title01__img {

  @include mq(md) {
    margin-top: rem(16);
  }
}

.title01__img img {

  @include mq(md) {
    aspect-ratio: 204.185 / 63.691;
    width: rem(204.185);
  }
}
