@use "global" as *;


.sns-wrap {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: rem(40);
}

.sns-wrap__insta {

}

.sns-wrap__insta img {

  @include mq(md) {
    aspect-ratio: 1 / 1;
    max-width: rem(48);
    height: rem(48);
  }
}

.sns-wrap__line {

}

.sns-wrap__line img {

  @include mq(md) {
    aspect-ratio: 1 / 1;
    max-width: rem(48);
    height: rem(48);
  }
}
