@use "global" as *;


.nav {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}

.nav__item {
  display: block;
  width: 100%;
  padding-block: rem(32);
  border-bottom: 1px solid #7B695A;

  color: $color-white;
  leading-trim: both;
  text-edge: cap;
  font-family: $font-family-en-1;
  font-size: rem(20);
  font-style: normal;
  font-weight: $bold;
  line-height: 1.4;
  letter-spacing: 0.04em;
  text-indent: 0.04em;
  text-transform: capitalize;
}

.nav__item:last-of-type {
  border-bottom: none;
}
